"use client";

import React, { useState } from "react";
import toast, { Toaster } from 'react-hot-toast';
import {
    Card,
    CardContent,
    CardHeader,
    CardTitle,
    CardFooter,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import {
    Select,
    SelectTrigger,
    SelectValue,
    SelectContent,
    SelectItem,
} from "@/components/ui/select";
import { Pencil, Trash2, Upload } from "lucide-react";

export default function CreateCategory({ onCategoryCreated }) {
    const [name, setName] = useState("");
    const [title, setTitle] = useState("");
    const [url, setUrl] = useState("");
    const [type, setType] = useState("");
    const [imageFile, setImageFile] = useState(null);
    const [preview, setPreview] = useState(null);

    const [errors, setErrors] = useState({});
    const [loading, setLoading] = useState(false);

    const validate = () => {
        const newErrors = {};
        if (!name.trim()) newErrors.name = "Name is required";
        if (!title.trim()) newErrors.title = "Title is required";
        if (!type.trim()) newErrors.type = "Type is required";
        if (!imageFile) newErrors.image = "Image is required";
        return newErrors;
    };
    const handleChange = (setter) => (e) => {
        const value = e.target.value;
        const sanitized = value.replace(/^[a-z]/, (match) => match.toUpperCase());
        setter(sanitized);
    };


    const handleImageChange = (file) => {
        if (file) {
            setImageFile(file);
            setPreview(URL.createObjectURL(file));
        }
    };

    const handleSubmit = async () => {
        setErrors({});
        const validationErrors = validate();
        if (Object.keys(validationErrors).length > 0) {
            setErrors(validationErrors);
            return;
        }

        setLoading(true);
        try {
            const token = localStorage.getItem("token");
            if (!token) throw new Error("Token not found");

            const formData = new FormData();
            formData.append("name", name);
            formData.append("title", title);
            formData.append("url", url.trim() === "" ? "" : url);
            formData.append("type", type);
            if (imageFile) formData.append("image", imageFile);

            const res = await fetch(
                "https://wiqiapi.testenvapp.com/api/admin/createCategory",
                {
                    method: "POST",
                    headers: {
                        Authorization: `Bearer ${token}`,
                    },
                    body: formData,
                }
            );

            const data = await res.json();
            if (!res.ok || !data.success) throw new Error(data.message || "Failed to create category");

            toast.success("Category created successfully!");

            // Reset form
            setName("");
            setTitle("");
            setUrl("");
            setType("");
            setImageFile(null);
            setPreview(null);

            // ✅ Notify parent to refresh table
            if (onCategoryCreated) onCategoryCreated();

        } catch (err) {
            toast.error(err.message || "Something went wrong");
        } finally {
            setLoading(false);
        }
    };

    return (
        <Card className="w-full">
            <CardHeader>
                <CardTitle className="text-xl font-bold text-primary">
                    Create New Category
                </CardTitle>
            </CardHeader>
            <CardContent className="space-y-5">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
                    {/* Name */}
                    <div>
                        <Label htmlFor="name">Name</Label>
                        <Input
                            id="name"
                            placeholder="Enter Name"
                            value={name}
                            onChange={handleChange(setName)}
                        />
                        {errors.name && (
                            <p className="text-sm text-red-600">{errors.name}</p>
                        )}
                    </div>

                    {/* Title */}
                    <div>
                        <Label htmlFor="title">Title</Label>
                        <Input
                            id="title"
                            placeholder="Enter Title"
                            value={title}
                            onChange={handleChange(setTitle)}
                        />
                        {errors.title && (
                            <p className="text-sm text-red-600">{errors.title}</p>
                        )}
                    </div>

                    {/* URL */}
                    <div>
                        <Label htmlFor="url">URL (optional)</Label>
                        <Input
                            id="url"
                            placeholder="Enter URL"
                            value={url}
                            onChange={(e) => setUrl(e.target.value)}
                        />
                    </div>

                    {/* Type */}
                    <div>
                        <Label htmlFor="type">Type</Label>
                        <Select
                            value={type}
                            onValueChange={(value) => setType(value)}
                        >
                            <SelectTrigger id="type" className="w-full cursor-pointer">
                                <SelectValue placeholder="Select Type" />
                            </SelectTrigger>
                            <SelectContent>
                                <SelectItem className={'cursor-pointer'} value="Wiqi Plus">Wiqi Plus</SelectItem>
                                <SelectItem className={'cursor-pointer'} value="Tv">Tv</SelectItem>
                                <SelectItem className={'cursor-pointer'} value="Radio">Radio</SelectItem>
                                <SelectItem className={'cursor-pointer'} value="Shops">Shops</SelectItem>
                                <SelectItem className={'cursor-pointer'} value="Reads">Reads</SelectItem>
                            </SelectContent>
                        </Select>
                        {errors.type && (
                            <p className="text-sm text-red-600">{errors.type}</p>
                        )}
                    </div>
                </div>

                {/* Image Upload */}
                <div className="flex flex-col gap-2">
                    <Label>Image</Label>
                    {preview ? (
                        <div className="relative w-32 h-32">
                            <img
                                src={preview}
                                alt="Preview"
                                className="w-32 h-32 rounded object-cover border"
                            />
                            <div className="absolute inset-0 flex items-center justify-center gap-2 bg-black/40 rounded opacity-0 hover:opacity-100 transition">
                                <label className="cursor-pointer bg-white p-2 rounded-full shadow hover:bg-gray-100">
                                    <Pencil className="w-4 h-4 text-blue-600" />
                                    <input
                                        type="file"
                                        accept="image/*"
                                        className="hidden"
                                        onChange={(e) =>
                                            handleImageChange(e.target.files[0])
                                        }
                                    />
                                </label>
                                <button
                                    type="button"
                                    onClick={() => {
                                        setImageFile(null);
                                        setPreview(null);
                                    }}
                                    className="bg-white p-2 rounded-full shadow hover:bg-gray-100"
                                >
                                    <Trash2 className="w-4 h-4 text-red-600" />
                                </button>
                            </div>
                        </div>
                    ) : (
                        <label className="flex items-center justify-center border border-dashed border-gray-400 rounded-lg p-6 cursor-pointer hover:bg-gray-50">
                            <Upload className="w-5 h-5 mr-2 text-gray-500" />
                            <span className="text-gray-600">Choose Image</span>
                            <input
                                type="file"
                                accept="image/*"
                                className="hidden"
                                onChange={(e) =>
                                    handleImageChange(e.target.files[0])
                                }
                            />
                        </label>
                    )}
                    {errors.image && (
                        <p className="text-sm text-red-600">{errors.image}</p>
                    )}
                </div>
            </CardContent>

            <CardFooter className="flex justify-end">
                <Button onClick={handleSubmit} disabled={loading}>
                    {loading ? "Creating..." : "Create Category"}
                </Button>
                <Toaster position="top-right" />
            </CardFooter>

        </Card>
    );
}
