"use client";

import { useEffect, useMemo, useState } from "react";
import {
    getCoreRowModel,
    getFilteredRowModel,
    getPaginationRowModel,
    getSortedRowModel,
    useReactTable,
} from "@tanstack/react-table";
import { Pencil, Trash2, Search, X, Link2 } from "lucide-react";
import { Button } from "@/components/ui/button";
import {
    Card,
    CardFooter,
    CardHeader,
    CardTable,
    CardTitle,
    CardToolbar,
} from "@/components/ui/card";
import { DataGrid } from "@/components/ui/data-grid";
import { DataGridTable } from "@/components/ui/data-grid-table";
import { DataGridPagination } from "@/components/ui/data-grid-pagination";
import { Input } from "@/components/ui/input";
import { ScrollArea, ScrollBar } from "@/components/ui/scroll-area";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";

const AllTypeCategory = ({refreshFlag }) => {
    const [categories, setCategories] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [pagination, setPagination] = useState({
        pageIndex: 0,
        pageSize: 10,
    });
    const [sorting, setSorting] = useState([]);
    const [searchQuery, setSearchQuery] = useState("");

    // ✅ Fetch categories
    useEffect(() => {
        const fetchCategories = async () => {
            try {
                const token = localStorage.getItem("token");
                if (!token) throw new Error("Access Denied: No token provided.");

                const response = await fetch(
                    "https://wiqiapi.testenvapp.com/api/admin/getCategory",
                    {
                        method: "GET",
                        headers: {
                            "Content-Type": "application/json",
                            Authorization: `Bearer ${token}`,
                        },
                    }
                );

                const data = await response.json();
                if (!response.ok || !data.success) {
                    throw new Error(data.message || "Failed to fetch categories");
                }

                if (Array.isArray(data?.data?.data)) {
                    setCategories(data.data.data);
                } else {
                    throw new Error("Invalid category data format");
                }
            } catch (err) {
                setError(err.message);
            } finally {
                setLoading(false);
            }
        };

        fetchCategories();
    }, [refreshFlag]);

    // ✅ Search filter
    const filteredData = useMemo(() => {
        if (!searchQuery) return categories;
        return categories.filter(
            (cat) =>
                cat.name?.toLowerCase().includes(searchQuery.toLowerCase()) ||
                cat.title?.toLowerCase().includes(searchQuery.toLowerCase()) ||
                cat.type?.toLowerCase().includes(searchQuery.toLowerCase())
        );
    }, [searchQuery, categories]);

    // ✅ Columns
    const columns = useMemo(
        () => [
            {
                id: "image",
                accessorFn: (row) => row.image,
                header: () => <span className="font-bold">Image</span>,
                cell: ({ row }) =>
                    row.original.image ? (
                        <img
                            src={row.original.image}
                            alt={row.original.name}
                            className="w-12 h-12 rounded object-cover border"
                            onError={(e) => {
                                e.currentTarget.src =
                                    "https://via.placeholder.com/80?text=No+Img";
                            }}
                        />
                    ) : (
                        <span className="text-gray-400 text-sm">No Image</span>
                    ),
                size: 100,
            },
            {
                id: "name",
                accessorFn: (row) => row.name,
                header: () => <span className="font-bold">Name</span>,
                cell: ({ row }) => (
                    <span className="font-bold truncate block max-w-[200px]" title={row.original.name}>
                        {row.original.name}
                    </span>
                ),
                size: 150,
            },
            {
                id: "title",
                accessorFn: (row) => row.title,
                header: () => <span className="font-bold">Title</span>,
                cell: ({ row }) => (
                    <TooltipProvider>
                        <Tooltip>
                            <TooltipTrigger asChild>
                                <span className="truncate block max-w-[350px] cursor-help">
                                    {row.original.title}
                                </span>
                            </TooltipTrigger>
                            <TooltipContent side="top" className="max-w-xs">
                                {row.original.title}
                            </TooltipContent>
                        </Tooltip>
                    </TooltipProvider>
                ),
                size: 350,
            },
            {
                id: "type",
                accessorFn: (row) => row.type,
                header: () => <span className="font-bold">Type</span>,
                cell: ({ row }) => (
                    <span className="capitalize truncate block max-w-[150px]" title={row.original.type}>
                        {row.original.type}
                    </span>
                ),
                size: 100,
            },
            {
                id: "actions",
                header: () => <span className="font-bold block text-right">Actions</span>,
                cell: ({ row }) => (
                    <div className="flex justify-end gap-3 items-center">
                        {row.original.url && (
                            <a
                                href={row.original.url}
                                target="_blank"
                                rel="noopener noreferrer"
                                className="text-blue-600 hover:text-blue-800 flex items-center gap-1 text-sm"
                            >
                                <Link2 size={16} /> Visit
                            </a>
                        )}

                        {/* ✅ Edit Tooltip */}
                        <TooltipProvider>
                            <Tooltip>
                                <TooltipTrigger asChild>
                                    <button className="text-blue-600 cursor-pointer hover:text-blue-800">
                                        <Pencil size={18} />
                                    </button>
                                </TooltipTrigger>
                                <TooltipContent>Edit</TooltipContent>
                            </Tooltip>
                        </TooltipProvider>

                        {/* ✅ Delete Tooltip */}
                        <TooltipProvider>
                            <Tooltip>
                                <TooltipTrigger asChild>
                                    <button className="text-red-600 cursor-pointer hover:text-red-800">
                                        <Trash2 size={18} />
                                    </button>
                                </TooltipTrigger>
                                <TooltipContent>Delete</TooltipContent>
                            </Tooltip>
                        </TooltipProvider>
                    </div>
                ),
                size: 100,
            }
        ]
    );

    const table = useReactTable({
        columns,
        data: filteredData,
        pageCount: Math.ceil((filteredData?.length || 0) / pagination.pageSize),
        getRowId: (row) => String(row._id),
        state: {
            pagination,
            sorting,
        },
        columnResizeMode: "onChange",
        onPaginationChange: setPagination,
        onSortingChange: setSorting,
        getCoreRowModel: getCoreRowModel(),
        getFilteredRowModel: getFilteredRowModel(),
        getPaginationRowModel: getPaginationRowModel(),
        getSortedRowModel: getSortedRowModel(),
    });

    // ✅ UI
    if (loading) {
        return (
            <div className="flex justify-center items-center min-h-[200px]">
                <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
                <span className="ml-3">Loading categories...</span>
            </div>
        );
    }

    if (error) {
        return (
            <div className="my-4 p-4 bg-red-100 text-red-700 rounded">
                <p className="font-bold">Error loading categories:</p>
                <p>{error}</p>
                <button
                    onClick={() => window.location.reload()}
                    className="mt-2 px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
                >
                    Retry
                </button>
            </div>
        );
    }

    return (
        <DataGrid
            table={table}
            recordCount={filteredData?.length || 0}
            tableLayout={{
                columnsPinnable: true,
                columnsMovable: true,
                columnsVisibility: true,
                rowBorder: true,
            }}
        >
            <Card>
                <CardHeader className="py-3.5">
                    <CardTitle>Categories</CardTitle>
                    <CardToolbar className="relative">
                        <Search className="size-4 text-muted-foreground absolute start-3 top-1/2 -translate-y-1/2" />
                        <Input
                            placeholder="Search categories..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            className="ps-9 max-w-[200px] w-full"
                        />
                        {searchQuery.length > 0 && (
                            <Button
                                mode="icon"
                                variant="ghost"
                                className="absolute end-1.5 top-1/2 -translate-y-1/2 h-6 w-6"
                                onClick={() => setSearchQuery("")}
                            >
                                <X />
                            </Button>
                        )}
                    </CardToolbar>
                </CardHeader>
                <CardTable>
                    <ScrollArea>
                        <DataGridTable />
                        <ScrollBar orientation="horizontal" />
                    </ScrollArea>
                </CardTable>
                <CardFooter>
                    <DataGridPagination />
                </CardFooter>
            </Card>
        </DataGrid>
    );
};

export default AllTypeCategory;
